.outer-container-register {
  position: relative;
  height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.outer-container-register::before {
  content: "";
  background-image: url("https://cdn.pixabay.com/photo/2012/12/29/21/11/sunrise-73074_960_720.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  position: absolute;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  filter: blur(5px);
  -webkit-filter: blur(5px);
  margin: -10px;
  /* z-index: -1; */
}

.content-container-register {
  position: relative;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
  max-width: 60rem;
  border-radius: 60px;
  padding-top: 1%;
  padding-bottom: 1%;
}

h3 {
  padding-top: 2rem;
}

.signup-form {
  max-width: 50rem;
  margin: 0 auto;
}
